
.dialogWrapper {
  position: fixed;
  background: rgba(0, 0, 0, 0.85);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#MyDialog {
  padding-top: 20px;
  width: 330px;
  margin: 0 auto;
  text-align: center;
}
#MyDialog > header {
    padding-left: 280px;
}
#MyDialog > header .close {
      cursor: pointer;
}
#MyDialog .land-nav {
    padding-bottom: 24px;
    margin-top: 20px;
    color: #959595;
    font-size: 14px;
}
#MyDialog .land-nav label {
      cursor: pointer;
      padding: 10px;
}
#MyDialog .land-nav .active {
      display: none;
}
#MyDialog .land-nav input[type="radio"] {
      display: none;
}
#MyDialog .formRow {
    margin: 10px;
}
#MyDialog .formRow input {
      font-size: 16px;
      width: 300px;
      border-radius: 4px;
      border: 1px solid #787878;
      padding: 10px 5px;
      font-family: sans-serif;
}
#MyDialog .formActions input {
    cursor: pointer;
    margin-top: 10px;
    padding: 6px;
    border: 1px solid #787878;
    border-radius: 4px;
}
#MyDialog .formActions .signUp-button {
    background-color: pink;
}
#MyDialog .formActions .login-button {
    background-color: green;
}
#MyDialog .formActions input[type="submit"] {
    width: 300px;
    font-size: 20px;
    color: #fff;
}



